*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}

body {
  box-sizing: border-box;
  font-family: Montserrat, sans-serif;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1;
}

.card {
  width: 60rem;
  background-color: #fff;
  border-radius: 1rem;
  grid-template-columns: 1fr 1fr;
  display: grid;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 37.5em) {
  .card {
    width: 80%;
    grid-template-columns: 1fr;
  }
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__details {
  flex-direction: column;
  gap: 2.5rem;
  margin: 3rem;
  display: flex;
}

@media only screen and (max-width: 37.5em) {
  .card__details {
    gap: 2rem;
  }
}

.card__category {
  text-transform: uppercase;
  letter-spacing: .5rem;
  color: #6c7289;
  font-size: 1.2rem;
  font-weight: 500;
}

.card__title {
  color: #1c232b;
  font-family: Fraunces, serif;
  font-size: 3.5rem;
}

.card__description {
  color: #6c7289;
  line-height: 1.8;
}

.card__price-box {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.card__price {
  color: #3c8067;
  font-family: Fraunces, serif;
  font-size: 3.5rem;
}

.card__discount {
  color: #6c7289;
  text-decoration: line-through;
}

.card__to-cart-btn {
  cursor: pointer;
  color: #fff;
  background-color: #3c8067;
  border: none;
  border-radius: .8rem;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem 2.5rem;
  font-family: inherit;
  font-weight: 700;
  transition: all .3s;
  display: flex;
}

.card__to-cart-btn:hover, .card__to-cart-btn:active {
  background-color: #244c3d;
}

.content {
  height: 100vh;
  background-color: #f2ebe3;
  position: relative;
}

